import { useContext, useRef, useState } from "react";
import { Contextapi } from "../App";
import "../styles.css";

export default function Captionbox() {
  const { data, setdata } = useContext(Contextapi);
  const [stime, setstime] = useState(0);
  const [etime, setetime] = useState(0);
  const [cap, setcap] = useState("");
  const capref = useRef();
  const startref = useRef("0");
  const endref = useRef();
  const maxdur = 19;
  if (parseInt(startref.current.value) < 0) {
    startref.current.value = "";
  }

  const addCaption = () => {
    const arr = [...data];
    for (let i = stime; i < etime; i++) {
      arr[i] = cap;
    }
    setdata(arr);
    capref.current.value = "";
    startref.current.value = "";
    endref.current.value = "";
  };
  return (
    <div>
      <div className="note">Max Duration: {maxdur} sec</div>
      <div className="cap">
        <div className="cap-main">
          <div>
            <label>ENTER YOUR CAPTION</label>
          </div>
          <textarea
            name="Text1"
            cols="25"
            rows="3"
            maxLength="100"
            placeholder="Enter the text you ant to add.."
            onChange={(e) => {
              setcap(e.target.value);
              const arr = [...data];
              for (let i = stime; i < etime; i++) {
                arr[i] = capref.current.value;
              }
              setdata(arr);
            }}
            ref={capref}
          ></textarea>
        </div>
        <div className="tim">
          <div className="tim-main">
            <div className="tim-content">
              <div className="tim-label">
                <label>START(sec)</label>
              </div>
              <div className="tim-input">
                <input
                  type="number"
                  placeholder="0"
                  onChange={(e) => {
                    if (startref.current.value >= maxdur) {
                      startref.current.value = "";
                    }
                    setstime(e.target.value);
                  }}
                  ref={startref}
                  min="0"
                />
              </div>
            </div>
          </div>
          <div className="tim-main">
            <div className="tim-content">
              <div className="tim-label">
                <label>END(sec)</label>
              </div>
              <div className="tim-input">
                <input
                  type="number"
                  placeholder="0"
                  onChange={(e) => {
                    if (endref.current.value <= startref.current.value) {
                      endref.current.value = "";
                      startref.current.value;
                    }
                    if (endref.current.value > maxdur) {
                      endref.current.value = maxdur;
                    }
                    setetime(e.target.value);
                  }}
                  ref={endref}
                  min={stime}
                />
              </div>
            </div>
            <div className="tim-button">
              <button onClick={addCaption}>Add</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
